html {
  /* font-size: calc(16px + (24 - 16) * ((100vw - 300px) / (1600 - 300))); */
  /* font-size: clamp(1rem, 0.6897rem + 0.6897vw, 1.5rem); */
  scroll-behavior: smooth;
}
/* .scrollable {
  scroll-behavior: smooth;
} */
.hero-bg {
  max-width: calc(100% - 130px);
  height: auto;
}
.icon-link {
  max-width: calc(100% - 100px);
  height: auto;
}

.home-text {
  /* font-size: clamp(0.5rem, 12vw + 1rem, 3rem); */
  /* font-size: clamp(2.5rem, 2.1897rem + 0.6897vw, 3rem); */
  font-size: clamp(3rem, -0.0508rem + 3.3898vw, 4rem);
}

.shadow-middle {
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.3);
}

.shadow-offset {
  box-shadow: 5px 5px 0px rgba(255, 255, 255, 0.8);
}

.btn-hover {
  position: relative;
}
.btn-hover::after {
  content: "";
  opacity: 0;
  transition: all 0.2s;
  height: 2px;
  width: 100%;
  background-color: #ff013e;
  position: absolute;
  bottom: 0;
  left: 0;
}
.btn-hover:hover::after {
  opacity: 1;
}

.hideMenuNav {
  display: none;
}
.showMenuNav {
  display: block;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: white;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

#particles {
  z-index: -1;
}

.progress-bar {
  position: fixed;
  z-index: 999;
  left: 0;
  right: 0;
  height: 5px;
  background: #ff013e;
  bottom: 0;
}

.glass-bg {
  /* From https://css.glass */
  background: rgba(200, 200, 200, 0.1);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.groceryBg {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("./assets/grocery-bg-sm.png") no-repeat center/cover;
}
.OnlineClassBg {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("./assets/onlineClassBg-sm.png") no-repeat center/cover;
}
.mathBg {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("./assets/mathBg-sm.png") no-repeat center/cover;
}
.ecommerceBg {
  background: url("./assets/ecommerce.png") no-repeat center/cover;
}
.taguroBg {
  background: url("./assets/taguroApp.png") no-repeat center/cover;
}
.mathAppBg {
  background: url("./assets/mathApp.png") no-repeat center/cover;
}

